import Vue from "vue";
import Vuex from "vuex";
import App from "./App.vue";
import BootstrapVue from "bootstrap-vue";
import store from "./store";
import Helpers from "./helpers";
import server from "./server";
import ColyseusClient from "./colyseusClient";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import Unicon from 'vue-unicons'
import {
  uniEditAlt, uniAngleDoubleDown
} from 'vue-unicons/src/icons'
Unicon.add([uniEditAlt, uniAngleDoubleDown]);
Vue.use(Unicon);

Vue.config.errorHandler = (error) => {
  console.error(error);
  server.leaveRoom();
}

// make store available in browser console for development purpose
if (window.webpackHotUpdate) {
  window.STORE = store;
  window.Helpers = Helpers;
  window.ColyseusClient = ColyseusClient;
}
// in production show some funny message ഹീ ഹീ ഹീ!!!
else {
  Helpers.browser.hackWarn();
}

Vue.use(Vuex);
Vue.use(BootstrapVue);

Vue.config.productionTip = false;

window.addEventListener("resize", Helpers.browser.calculateSize);

new Vue({
  render: h => h(App)
}).$mount("#app");
