<template>
  <div
    :class="[
      className,
      strikedPlayerIndex ? 'strikedPlayer' + strikedPlayerIndex : ''
    ]"
    @click="play(cellData.number)"
  >
    <div :class="['number']">{{ cellData.number }}</div>
  </div>
</template>
<script>
import store from "@/store";
import Server from "@/server";

export default {
  data: () => {
    return {
      striked: false
    };
  },
  methods: {
    play(number) {
      if (this.isMyturn) {
        this.striked = true;
        Server.playMyTurn(number);
      }
    }
  },
  computed: {
    className: function() {
      const strikedCells = store.getters.allStrikedCells;
      const isStriked =
        (strikedCells.indexOf(this.cellData.number) >= 0 || this.striked) &&
        !this.isIamNewPlayer;
      return ["cellBox", isStriked ? "striked" : ""];
    },
    isMyturn: () => store.getters.isMyturn,
    isIamNewPlayer: () => store.getters.isIamNewPlayer,
    strikedPlayerIndex: function() {
      var players = store.state.room.myRoom;
      var playerIndex = 1;
      for (var playerId in players) {
        if (players[playerId].strikedCells.indexOf(this.cellData.number) >= 0)
          return playerIndex;
        playerIndex++;
      }
      return 0;
    }
  },
  props: ["cellData"]
};
</script>
