<template>
  <div id="app">
    <Loader />
    <SaveNameScreen v-if="!playerNameSaved" />
    <GameBoard v-else-if="room.myRoom" />
    <MainPage v-else />
  </div>
</template>
<style lang="scss" src="@/assets/css/bingo.scss"></style>

<script>
import GameBoard from "./components/GameBoard.vue";
import MainPage from "./components/MainPage.vue";
import Loader from "./components/Loader.vue";
import SaveNameScreen from "./components/SaveNameScreen.vue";
import store from "@/store";

export default {
  computed: {
    room: () => store.state.room,
    playerNameSaved: () => store.state.player.playerNameSaved
  },
  components: {
    GameBoard,
    MainPage,
    SaveNameScreen,
    Loader
  },
  created: () => {
    store.dispatch("setPlayer");
  }
};
</script>
